<template>
  <b-form class="ml-5 mt-5 mr-5 mb-5" v-if="!hasToken">
    <b-form-group
        id="input-group-1"
        label="클라이언트 ID"
        label-for="input-1"
    >
      <b-form-input
          id="input-1"
          v-model="form.client_id"
          type="text"
          placeholder="Enter Client Id"
      ></b-form-input>
    </b-form-group>

    <b-form-group
        id="input-group-2"
        label="클라이언트 key"
        label-for="input-2"
    >
      <b-form-input
          id="input-2"
          v-model="form.client_key"
          type="password"
          placeholder="Enter Client Key"
      ></b-form-input>
    </b-form-group>

    <b-button @click="getToken">
      getToken
    </b-button>
  </b-form>
</template>

<script>
import axios from "axios";

export default {
  name: "Token",
  data() {
    return {
      form: {
        client_id: '',
        client_key: '',
      },
    }
  },
  created() {
    this.base_URL = `${process.env.VUE_APP_TOKEN_SERVER}` === "undefined" ? '' : `${process.env.VUE_APP_TOKEN_SERVER}`
    console.log(process.env)
  },
  props: {
    hasToken : {
      type: Boolean, default: false
    }
  },
  methods: {
    getToken() {
      const self = this
      const authHeader = 'Basic ' + btoa(`${this.form.client_id}:${this.form.client_key}`);
      let data = JSON.stringify({
        grant_type: 'client_credentials'
      });
      axios.post(`${this.base_URL}/token`, data, {
        headers: {
          'Content-Type': 'application/json',
          'Authorization': authHeader
        }
      }).then(function (resp) {
        if (resp.status === 200) {
          self.$notify({
            duration: 3000,
            group: 'foo',
            title: 'get token success',
            text: 'statusText : ' + resp.statusText +'\n' + 'status : ' + resp.status
          });
          self.$emit("setToken", resp.data.access_token)
        }
      })
    },
  }
}
</script>

